@media screen and (min-device-width: 320px) and (max-device-width:600px) {

    .chkicons {
        color: rgb(4, 4, 4);
        position: relative;
        left: 120px;
        font-size: 50px;
        top: 23px;
        position: fixed;
        cursor: pointer;

    }

    .chkicons1 {
        color: rgb(255, 255, 255);
        position: relative;
        left: 170px;
        padding-right: 5px;
       /* f position: fixed;
        cursor: pointer; */

    }

    .sidenav {
        opacity: 0;
    }
}


@media screen and (min-device-width: 600px) and (max-device-width:1600px) {

    .chkicons {
        opacity: 0;

    }
    .chkicons1{
        opacity: 0;
    }
    .sidenav {
        opacity: 1;
    }

}